@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@500&family=Open+Sans:wght@300&family=STIX+Two+Math&display=swap);
html{
    background-color: #1d1d1d;
}

body{
    background: #1d1d1d url(https://gamepedia.cursecdn.com/deadbydaylight_gamepedia_en/0/05/Tbl-bg.jpg);
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    border-color: red;
}
*::-webkit-scrollbar{
    width: 10px;
    height: 7px;
}
*::-webkit-scrollbar-thumb{
    background:#cecece70;
    border-radius: 10px;
}
*::selection{
    background-color: #ff2a2b;
    color: #ffffff;
}
a:link{
    text-decoration: none;
}

.title{
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0 0 0;
    max-width: 100%;
    max-height: 250px;
    font-family: 'STIX Two Math', serif;
}
.title h1{
    text-shadow:0px 0px 3px #ffffff;
    transition: .5s;
    cursor:default;
}
.title h1:hover{
    text-shadow:0px 0px 6px #ffffff;
}

.title img{
    margin-top: 41px;
    max-width: 60px;
}
.title h1{
    margin: 47px 20px 0 0;
    font-size: 55px;
}

.scroll{
    width: 50px;
    height: 50px;
}

.line2{
    height: 3px;
    max-width: 700px;
    background: #ffffff;
    border: none;
    opacity: .3;
}
.container-nav{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    background:#1f2020 url(https://us.123rf.com/450wm/naropano/naropano1606/naropano160600579/58760029-textura-de-fondo-oscuro-en-blanco-para-el-dise%C3%B1o-bordes-oscuros.jpg?ver=6);
    height: 70px;
    flex-wrap: wrap;
    position: fixed;
    width: 100%;
    box-shadow: 0 2px 4px #000000;
    z-index: 100;
    font-family: 'Heebo', sans-serif;
}

.container-nav img{
    width: 80px;
    margin: 0 50px;
    cursor: pointer;
}


.container-nav p {
    color: #ffffff;
    text-shadow:0px 0px 3px #ffffff;
    transition: .5s;
    margin: 0;
    font-size: 25px;
    cursor: pointer;
}
.container-nav p:hover{
    text-shadow:0px 0px 18px #ffffff;
    font-size: 30px;
}


.buy{
    background-color: #ff2a2b;
    padding: 18px;
    margin: 0;
    transition: 0.2s;
    border: none;
    color: #ffffff;
    font-size: 22px;
    text-shadow:0px 0px 8px #ffffff;
}
.buy:hover{
    background-color:#b11919;
    text-decoration: none;
    transition: .5s;
    text-shadow:0px 0px 18px #ffffff;
}

@media only screen and (max-width:930px){
    .p-responsive{
        display: none;
    }
}
@media only screen and (max-width:700px){
    .p-responsive2{
        display: none;
    }
}

@media only screen and (max-width:430px){
    .buy{
        font-size: 15px;
        padding: 23px;
    }
    
}
.container-info{
    color: #ffffff;
    padding: 10px;
    margin: 100px 20px 100px 20px;
    margin-top: 50px;
}

.info-article{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    transition: all 0.25s;
    margin-bottom: 40px;
    border-radius: 10px;
}

.info-article img{
    max-width: 400px;
    margin: 40px;
}
.info-article:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow: 10px 20px 40px rgba(105, 105, 105, 0.2);
}

.info-article div{
    max-width: 600px;
    min-height: 400px;
    font-family: 'Open Sans', sans-serif;
}

.videoyt{
    display: flex;
    justify-content: center;
    margin: 110px 0 0 0;
}

.videoyt iframe{
    transition: .5s;
}
.videoyt iframe:hover{
    box-shadow:0px 0px 18px #ffffff;
}

.line{
    height: 2px;
    background: #ffffff;
    border: none;
    opacity: .5;
    display: none;
}

@media only screen and (max-width:1319px){
    .line{
        display: block;
    }
}

@media only screen and (max-width:930px){
    .container-info{
        margin-top: 0px;
    }
}

@media only screen and (max-width:400px){
    .info-article img{
        max-width: 250px;
        margin: 40px;
    }
}

.container{
    background: #000000 url(https://i.pinimg.com/originals/e8/8f/30/e88f30d66482e196c7096c4c44132e1c.jpg);
    color: #ffffff;
    max-width: 1100px;
    max-height: 450px;
    margin: 25px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    overflow: auto;
    border-radius: 10px;
    border: #e9e9e9 solid 2px;
    box-shadow: 4px 4px 8px 1px #616161;
}

.card-container{
    margin: 25px;
}
.card-container:hover figure{
    -webkit-transform: perspective(600px) rotateY(180deg);
            transform: perspective(600px) rotateY(180deg);
}
.card-container:hover figure img{
    -webkit-filter: blur(2px);
            filter: blur(2px);
}


.card-container a{
    display: inline-block;
    color: #ffffff;
}

.card-container a:hover{
    color: #ffffff;
}

figure{
    width: 200px;
    height: 200px;
    margin: 0;
    position: relative;
    transition: all ease .5s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: perspective(600px) rotateY(0deg);
            transform: perspective(600px) rotateY(0deg);
}

figure .face, figure .back {
    width: 100%;
    height: 100%;
    transition: all ease .5s;
    
}

figure .face {
    display: block;
}
figure .back{
    padding: 15px;
    position: absolute;
    top: 0;
    -webkit-transform: perspective(600px) rotateY(180deg);
            transform: perspective(600px) rotateY(180deg);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    overflow: auto;
}

figure .back hr{
    height: 2px;
    background: #ffffff;
    border: none;
    opacity: .5;
}

figure .back::-webkit-scrollbar{
    width: 5px;
    height: 7px;
}
figure .back::-webkit-scrollbar-thumb{
    background:#cecece70;
    border-radius: 10px;
}
.container::-webkit-scrollbar{
    width: 7px;
    height: 7px;
}
.container::-webkit-scrollbar-thumb{
    background:#cecece9d;
    border-radius: 10px;
}

.back h3{
    font-family: 'STIX Two Math', serif;
}
.back p{
    font-family: 'Open Sans', sans-serif;
}
.container-footer{
    background: #1d1d1d;
    color: #ffffff;
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
    margin-top: 90px;
}
.container-footer a{
    color: #ffffff;
}

.title-footer {
    font-size: 20px;
    margin-bottom: 30px;
}


.container-ul i{
    font-size: 40px;
    margin: 10px 20px;
    transition: .5s;
}
.container-ul i:hover{
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
    color: #ff2a2b;
}

.container-footer h6{
    font-size: 10px;
}

.p-footer { 
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 20px 0;
    flex-wrap: wrap;
}
.p-footer p{
    font-size: 15px;
    margin: 10px 20px;
    color: #474747;
    cursor: pointer;
}
.p-footer p:hover{
    color: #ffffff;
}
.slider-container {
  width: 100%;
  height: 100%;
}
  
.slide {
  width: 100%;
  max-height: 405px;
}
  
.slide img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@media only screen and (max-width:900px){
  .slide {
    max-height: 100%;
  }
}
.containter-section{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    padding-top: 80px;
}
.containter-section h1{
    text-shadow:0px 0px 3px #ffffff;
    transition: .5s;
    cursor:default;
    margin-bottom: 25px;
    align-items: center;
    justify-content: center;
}
.containter-section h1:hover{
    text-shadow:0px 0px 6px #ffffff;
}

.container-dlcs{
    background-color: #6868682c;
    height: 500px;
    max-width: 1150px;
    padding: 20px 0px;
    overflow: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.container-dlcs img{
    width: 300px;
    margin: 10px 30px;
    transition: .5s;
    opacity: .8;
}
.container-dlcs img:hover{
    width: 340px;
    opacity: 1;
}

@media only screen and (max-width:430px){
    .container-dlcs img{
        margin: 10px 0px;
    }
    .container-dlcs{
        padding: 0;
    }
}

@media only screen and (max-width:1319px){
    .container-dlcs{
        max-width: 800px;
    }

}

@media only screen and (max-width:575px){
    .containter-section h1{
        font-size: 29px;
    }
}
.modalnav{
    display: none;
    background: #00000088;
    color: #000000;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 200;
    justify-content: center;
    align-items: center;
}
.modalnav-open{
    display: flex;
    
}

.modalnav-style{
    background: #eeeeee;
    border-radius: 15px;
    min-width: 300px;
    width: 500px;
    height: 600px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.modalnav-style img{
    max-width: 250px;
}

.item-modal{
    width: 300px;
    margin: 20px;
    text-align: center;
}
.boton-close{
    align-self: flex-end;
    border-radius: 5px;
}


